import React from 'react'

import Footer from './Footer'
import avatar from '../assets/images/avatar3.jpeg'

class Header extends React.Component {
    render() {
        return (
            <header id="header">
                <div className="inner">
                    <a href="#" className="image avatar"><img src={avatar} alt="" /></a>
                    <h1><strong>I am Adrian Berger,</strong><br />Media Technology student
                    at KTH</h1>
                </div>
                <Footer />
            </header>
        )
    }
}

export default Header
